.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

@font-face {
    font-family: "Pokemon Solid";
    src: url("f4d1593471d222ddebd973210265762a.eot"); /* IE9*/
    src: url("f4d1593471d222ddebd973210265762a.eot?#iefix")
            format("embedded-opentype"),
        /* IE6-IE8 */ url("f4d1593471d222ddebd973210265762a.woff2")
            format("woff2"),
        /* chrome、firefox */ url("f4d1593471d222ddebd973210265762a.woff")
            format("woff"),
        /* chrome、firefox */ url("f4d1593471d222ddebd973210265762a.ttf")
            format("truetype"),
        /* chrome、firefox、opera、Safari, Android, iOS 4.2+*/
            url("f4d1593471d222ddebd973210265762a.svg#Pokemon Solid")
            format("svg"); /* iOS 4.1- */
}

.tittle {
    text-align: center;
    font-family: "Pokemon Solid" !important;
    /* font-size: 16px; */
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.2px;
    -moz-osx-font-smoothing: grayscale;
    color: #f5dc14;
}
